/* create css */
.Create{
  width:80%;
  /* background-color: red; */
  margin-left:auto;
  margin-right:auto;
  text-align: center;
  margin-top:14vh;
}
.Create p{
  font-size: 22px;
  margin-top:15px;
}
.Create input{
  width:300px;
  height: 30px;
  border-radius:5px;
  border-style:none;
  box-shadow:1px 1px 3px 0.1px grey;
}
.Create input:focus{
  outline:none;
}
.Create textarea{
  width:300px;
  height: 100px;
  border-radius:5px;
}
.Create textarea:focus{
  outline:none;
}
.Create select{
  width:100px;
  height:30px;
  text-align: center;
  border-style: none;

  box-shadow: 1px 1px 3px 0.1px grey;
  
}
.Create select:focus{
  outline:none;
}
.Create button{
  width: 100px;
  height:30px;
  background-color:rgb(247, 34, 34);
  color:white;
  border:none;
  border-radius: 5px;
  display:block;
  margin-left:auto;
  margin-right: auto;
  margin-top:15px;
  

}
.Create button:hover{
  cursor: pointer;
  background-color: rgb(222, 5, 5);
}
