/* footer css */
#footer-container {
  /* max-width: 1200px; */
  margin: 0;
  background-color: #333;
  position:relative;
  bottom: 0px;
  /* padding: 20px; */
}

footer {
  max-width: 1200px;
  margin-left:auto;
  margin-right:auto;
  color: #fff;
  padding: 20px 20px;
}

.footer-content {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.logo img {
  max-width: 100px;
}

.about-us,
.follow-us {
  flex: 1;
  margin: 0 20px;
}

.about-us h3,
.follow-us h3 {
  font-size: 18px;
}

.about-us p {
  font-size: 14px;
}

.follow-us ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.follow-us li {
  margin-bottom: 5px;
}
.follow-us li img{
  width:1.4rem;
  position: relative;
  top:5px;
}

.follow-us a {
  text-decoration: none;
  color: #fff;
  font-size: 14px;
}

.follow-us a:hover {
  text-decoration: underline;
}

.create-foot{
  position: fixed;
  bottom:0px;
  width: 100%;
  left:0px;
}